import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table, Button, Modal, ModalBody, ModalHeader, ModalFooter, Input } from 'reactstrap';
import { Redirect } from 'react-router-dom';
import Cookies from 'universal-cookie';
const cookies = new Cookies();

class FinancialOpportunities extends Component {
  constructor(props) {
    super(props);

    this.state = {
      opportunities: [],
      isLoggedIn: false,
      isAdmin: false,
      modalOpen: false,
      modalHead: '',
      recNew: false,
      fnProvider: '',
      fnTitle: '',
      fnDuration: '',
      fnContact: '',
      fnId: '',
    };
    this.showDetailedModal = this.showDetailedModal.bind(this);
    this.addRecord = this.addRecord.bind(this);
    this.updateRecord = this.updateRecord.bind(this);
    this.deleteRecord = this.deleteRecord.bind(this);
    this.toggleModal = this.toggleModal.bind(this);
  }

  fetchOpportunities() {
    fetch('https://missionaustralia-back.herokuapp.com/getOpportunities?token=' + cookies.get('token'))
      .then(response => response.json())
      .then(resData => {
        console.log(resData)
        if (resData.response === 200) {
          this.setState({
            opportunities: resData.data !== undefined ? resData.data : []
          });
        } else {
          alert("Error fetching opportunities")
        }
      });
  }

  showDetailedModal(row) {
    if (row === null) {
      this.setState({
        modalHead: 'New Opportunity',
        modalBody: 'Demo Body',
        recNew: true,
        fnProvider: '',
        fnTitle: '',
        fnDuration: '',
        fnContact: '',
        fnId: '',
      })
    } else {
      this.setState({
        modalHead: row.title,
        modalBody: JSON.stringify(row),
        recNew: false,
        fnProvider: row.provider,
        fnTitle: row.title,
        fnDuration: row.duration,
        fnContact: row.contact,
        fnId: row._id,
      })
    }
    this.toggleModal();
  }

  toggleModal() {
    this.setState({
      modalOpen: !this.state.modalOpen,
    });
  }

  updateFields(evt, val) {
    if (val === 1) {
      this.setState({
        fnProvider: evt.target.value
      });
    } else if (val === 2) {
      this.setState({
        fnTitle: evt.target.value
      })
    } else if (val === 3) {
      this.setState({
        fnDuration: evt.target.value
      })
    } else if (val === 4) {
      this.setState({
        fnContact: evt.target.value
      })
    }
  }

  addRecord(){
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ provider: this.state.fnProvider, title: this.state.fnTitle, duration: this.state.fnDuration, contact: this.state.fnContact })
    };
    fetch('https://missionaustralia-back.herokuapp.com/addOpportunity', requestOptions)
      .then(response => response.json())
      .then(resData => {
        console.log(resData)
        if (resData.response === 200) {
          alert("Record Successfully added");
          this.fetchOpportunities();
          this.toggleModal();
        } else {
          alert("Error while adding the record");
          this.toggleModal();
        }
      });
  }

  updateRecord(){
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ provider: this.state.fnProvider, title: this.state.fnTitle, duration: this.state.fnDuration, contact: this.state.fnContact, _id: this.state.fnId, token: cookies.get('token') })
    };
    fetch('https://missionaustralia-back.herokuapp.com/updateOpportunity', requestOptions)
      .then(response => response.json())
      .then(resData => {
        console.log(resData)
        if (resData.response === 200) {
          alert("Record Successfully updated");
          this.fetchOpportunities();
          this.toggleModal();
        } else {
          alert("Error while updating the record");
          this.toggleModal();
        }
      });
  }

  deleteRecord(){
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ _id: this.state.fnId, token: cookies.get('token') })
    };
    fetch('https://missionaustralia-back.herokuapp.com/deleteOpportunity', requestOptions)
      .then(response => response.json())
      .then(resData => {
        console.log(resData)
        if (resData.response === 200) {
          alert("Record Successfully deleted");
          this.fetchOpportunities();
          this.toggleModal();
        } else {
          alert("Error while deleting the record");
          this.toggleModal();
        }
      });
  }

  componentWillMount() {
    if (cookies.get('token') !== undefined && cookies.get('token') !== null) {
      this.setState({
        isLoggedIn: true
      });
    }

    if (cookies.get('admin') !== 'false'){
      this.setState({
        isAdmin: true
      });
    } else {
      this.setState({
        isAdmin: false
      });
    }

  }

  componentDidMount() {
    this.fetchOpportunities()
  }

  render() {
    if (this.state.isLoggedIn) {
      return (
        <div className="animated fadeIn">
          <Row>
            <Col>
              <Card>
                <CardHeader>
                  <i className="fa fa-align-justify"></i> Financial Opportunities
              </CardHeader>
                <CardBody>
                  <Table hover bordered striped responsive size="sm">
                    <thead>
                      <tr>
                        <th>Financial Scheme Provider</th>
                        <th>Title</th>
                        <th>Financial Scheme Duration</th>
                        <th>Contact Details</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.opportunities.map((opportunity, i) => {
                        return (
                          <tr key={opportunity._id} onClick={this.state.isAdmin? () => this.showDetailedModal(opportunity) : null}>
                            <td>{opportunity.provider}</td>
                            <td>{opportunity.title}</td>
                            <td>
                              {opportunity.duration}
                            </td>
                            <td>
                              {opportunity.contact}
                            </td>
                          </tr>);
                      })}
                    </tbody>
                  </Table>
                  <Modal isOpen={this.state.modalOpen} toggle={this.toggleModal}
                    className={'modal-primary ' + this.props.className}>
                    <ModalHeader toggle={this.togglePrimary}>{this.state.modalHead}</ModalHeader>
                    <ModalBody>
                      <Table>
                        <tr>Provider<Input type="text" onChange={evt => this.updateFields(evt, 1)} value={this.state.fnProvider}/></tr>
                        <tr>Title<Input type="text" onChange={evt => this.updateFields(evt, 2)} value={this.state.fnTitle}/></tr>
                        <tr>Duration<Input type="text" onChange={evt => this.updateFields(evt, 3)} value={this.state.fnDuration}/></tr>
                        <tr>Contact<Input type="text" onChange={evt => this.updateFields(evt, 4)} value={this.state.fnContact}/></tr>
                      </Table>
                      <input type="hidden" value={this.state.fnId}/>
                    </ModalBody>
                    <ModalFooter>
                      {this.state.recNew === true ?
                        <Button color="success" onClick={this.addRecord}>Add</Button>
                        : <Button color="info" onClick={this.updateRecord}>Update</Button>}
                      {' '}
                      <Button color="danger" onClick={this.deleteRecord}>Delete</Button>
                    </ModalFooter>
                  </Modal>
                  {this.state.isAdmin ?
                    <div style={{ display: 'flex', width: '10%' }}>
                      <Button className="btn btn-success" style={{ flex: 1, margin: '5px' }} onClick={() => this.showDetailedModal(null)}>Add</Button>
                    </div>
                    : null}
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      );
    } else {
      return <Redirect to='/login' />
    }
  }
}

export default FinancialOpportunities;